import { Alert, Snackbar } from "@mui/material";
import { AxiosError } from "axios";
import { IPropsError } from "pages/Interface";
import { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import { adminGetUsers, adminLoginVerify } from "services/Adminservice";

const Ausers = () => {
    const nav = useNavigate()
    const [snackopen, setSnackOpen] = useState<IPropsError>({ open: false, severity: undefined, message: "" })
    const [orderKey, setOrderKey] = useState<Array<any>>([]);

    useEffect(() => {
        (async function () {
            if (localStorage.getItem("admintoken")) {
                try {
                    const verify = await adminLoginVerify(String(localStorage.getItem("admintoken")));
                    if (verify.data.status === "Success") {
                        const { _id:adminId} = verify.data.data
                        const userList = await adminGetUsers(adminId)                        
                    } else {
                        nav("/admin/login")
                    }
                } catch (error: unknown) {
                    setSnackOpen({ open: true, severity: "warning", message: (error as AxiosError).message })
                }
            } else {
                nav("/admin/login")
            }
        })();
    }, [])

    const handleClose = (_event?: React.SyntheticEvent | Event, reason?: string) => {
        if (reason === 'clickaway') {
            return;
        }
        setSnackOpen({ open: false, severity: undefined, message: "" });
    };
    return (
        <>
            <div>Admin User</div>
            {snackopen.open && <Snackbar open={snackopen.open} autoHideDuration={6000} onClose={handleClose} anchorOrigin={{ vertical: 'bottom', horizontal: 'right' }}>
                <Alert onClose={handleClose} severity={snackopen.severity} sx={{ width: '100%' }}>
                    {snackopen.message}
                </Alert>
            </Snackbar>}
        </>
    )
}

export default Ausers;